export const RECHARGE_ENERGY_SKILL = "01";              // 
export const RECHARGE_ENERGY_BURST = "02";              // 
export const RECHARGE_ENERGY_PASSIVE = "03";            // 
export const RECHARGE_ENERGY_CONSTELLATION = "04";      // 
export const RECHARGE_ENERGY_WEAPON = "06";             // 
export const RECHARGE_PARTICLE_SKILL = "11";            // 
export const RECHARGE_PARTICLE_PASSIVE = "13";          // 
export const RECHARGE_PARTICLE_CONSTELLATION = "14";    // 
export const RECHARGE_PARTICLE_FAVONIUS = "16";         // 
export const RECHARGE_PARTICLE_RESONANCE = "18";        // 
